.header {
    // display: none;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 101;
	align-items: center;
	padding-top: 100px;
    padding-bottom: 10px;
    /* 
    In order to achieve better performance
    we are not changing paddings or height but just scaling this pseudo-element
    since our design allows us to do so
    */
    /* 
    I didn't animate height, to achieve smoother animation
    p.s. I've just came up with this practice, if you find it wrong then I will not use it anymore.
    However I thought that in this particular case I could use it.
    */
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform-origin: center top;
        transform: scale3d(1, 1, 1);
        transition: transform 0.6s ease-in;
        background-color: $color-gray;
        z-index: -1;
        pointer-events: none;
    }
}

.header__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.logo {
    transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
    transition: transform 0.4s ease-out;
    line-height: 1;
}
.logo__pic {
    width: 150px;
    height: 150px;
}

.menu-list {
    display: flex;
}
.menu-list__item {
    position: relative;
    cursor: pointer;
}
.menu-list__item + .menu-list__item {
    margin-left: 32px;
}
.menu-list__svg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-59%, -64%) scale3d(0, 0, 1);
    width: 91px;
    height: 145px;
    transform-origin: right top;
    // transform-origin: center;
    opacity: 0;
    transition: opacity 0.3s ease-out, transform 0.6s ease-out ;
    z-index: -1;
    pointer-events: none;
}
.menu-list__item-link {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
}

.menu-list__item--active, 
.menu-list__item:not(.menu-list__item--active):hover,
.menu-list__item:not(.menu-list__item--active):focus {
    .menu-list__svg {
        transform: translate(-59%, -64%) scale3d(1, 1, 1);
        opacity: 1;
        transform-origin: left bottom;
    }
}


.menu__pic {
    display: none;
}

.close-btn {
    display: none;
    line-height: 1;
    transition: opacity 0.4s ease-out;
    &:hover, &:focus {
        opacity: 0.65;
    }
    &__icon {
        width: 20px;
        width: 20px;
    }
}
.burger-menu {
    display: none;
    transition: opacity 0.4s ease-out;
    &:hover, &:focus {
        opacity: 0.65;
    }
    &__icon {
        width: 26px;
        height: 18px;
    }
}



/* 
When we scroll down more than certain amount then set "header--active" to the header
*/
.header--active {
    &::before {
        transform: scale3d(1, 0.9, 1);
    }
    .logo {
        transform: scale3d(0.5, 0.5, 1) translate3d(0, -100%, 0);
    }
    /* 
    No transition here, since changing height will cause repainting.
    Therefore I didn't animate height, to achieve smoother animation
    p.s. I've just came up with this practice, if you find it wrong then I will not use it anymore.
    However I thought that in this particular case I could use it.
    */
    .header__container {
        height: 50px;
    }
    
}


