.progress-tracker {
    position: fixed;
    bottom: 25px;
    right: 25px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid currentColor;
    transition: opacity 0.4s ease-out;
    z-index: 100;
}

.progress-tracker__top-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s ease-out;
}
.back-to-top {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: $color-gray;
    border: none;
    border-radius: 50%;
}
.back-to-top__icon {
    width: 18px;
    height: 24px;
}


.progress-tracker__progress {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    opacity: 1;
    pointer-events: all;
    border-radius: 50%;
    background-color: $color-gray;
    transition: opacity 0.3s ease-out;
}
.progress-tracker__progress-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: currentColor;
}

.progress-tracker--full {
    .progress-tracker__top-btn {
        opacity: 1;
        pointer-events: all;
    }
    .progress-tracker__progress {
        opacity: 0;
        pointer-events: none;
    }
    &:hover {
        opacity: 0.6;
    }
}


