// Skills section styles
.skills__title {
    margin-bottom: 30px;
}

.skills__row {
    display: grid;
    min-width: 0; // Fix for bug
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
    row-gap: 20px;
}
.skill {
    grid-column: span 3;
}
.skill__name {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.2;
    margin-bottom: 6px;
}


