.preloader {
    position: fixed;
    // display: none;
    z-index: 9999;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #fff;
    transition: opacity 0.6s ease-out;
    &--loaded {
        opacity: 0;
    }
    &--hidden {
        opacity: 0;
        z-index: -1;
        pointer-events: none;
    }
}

.preloader__pic-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    // transform: translate3d(calc(-100% + var(--prealoder-x)), 0, 0);
    transform: translate3d(calc(-100% + var(--prealoder-x)), calc(100% - var(--prealoder-y)), 0);
    transition: transform linear;
    transition-duration: 2s;
    will-change: transform;
}
